<template>
  <div class="content">
    <div>
      <van-nav-bar left-text="返回" :title="mhurlN" left-arrow @click-left="back" />
    </div>
    <div class="content-box" ref="contentBox">
      <div class="pho-box">
        <div class="pho">
          <!-- <img v-for="(img,index) in imageList" :key="index" v-lazy="img" > -->
          <img
            v-for="(item,index) in contentData.list"
            :key="index"
            class="auto-img"
            preload="800px"
            v-lazy="item.img"
            loading="加载中"
          />
          <!-- <img  :src="item.img" alt /> -->
        </div>
      </div>
      <div class="btn">
        <van-button round color="linear-gradient( 150deg, white 1% , red 90% )" @click="goto">上一章</van-button>
        <van-button
          round
          color="linear-gradient(180deg, #999, #6149f6)"
          @click="getParticulars()"
        >返回目录</van-button>
        <van-button round color="linear-gradient(150deg, red 10% , white 99% )" @click="getto">下一章</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("contentModule");

export default {
  name: "contents",
  data() {
    return {
      i: 0,

      // 章节列表
      mhurl: [],
      // 章节名
      mhurlN: [],
      // 章节长度
      mhurlL: [],
      // 章节id
      conUrl: [],
      // 漫画id
      mhUrl: ""
    };
  },
  computed: {
    ...mapState(["contentData"])
  },
  methods: {
    ...mapMutations(["getcontentData", "mhurl1", "resetState"]),

    back() {
      this.$router.go(-1);
    },

    getParticulars() {
      this.$router.push({
        name: "particulars",
        query: {
          url: this.mhUrl
        }
      });
    },

    gotoSection() {
      let self = this;

      let i = this.i;

      let URL = this.$route.query.mhUrl;
      // console.log("URL ==>> ", URL);

      if (this.$route.query.i != undefined) {
        // console.log("this.i ==>> ", this.i);

        // 漫画id
        let mhUrl = this.$route.query.mhUrl;
        // console.log("mhUrl ==>> ", mhUrl);

        self.mhUrl = mhUrl;
        // console.log(" self.mhUrl ==>> ", self.mhUrl);

        this.axios({
          methods: "GET",
          url: "http://api.pingcc.cn/?mhurl1=" + mhUrl
        }).then(results => {
          // console.log("results ==>> ", results);

          let URL = this.$route.query.mhUrl;
          // console.log("URL ==>> ", URL);

          self.mhurl = results.data.list;
          // console.log("mhurl1 ==>> ", self.mhurl);

          // console.log("this.i ==>> ", this.i);

          self.mhurlN = self.mhurl[this.i].num;
          // console.log("mhurlN ==>> ", self.mhurlN);

          self.conUrl = self.mhurl[this.i].url;
          // console.log("conUrl ==>> ", self.conUrl);

          let likeMovieData = localStorage.getItem("enshrineData");
          likeMovieData = likeMovieData ? JSON.parse(likeMovieData) : [];
          // console.log("likeMovieData ==>> ", likeMovieData);

          for (let i = 0; i < likeMovieData.length; i++) {
            if (likeMovieData[i].mhUrl == URL) {
              likeMovieData[i].i = this.i;
              likeMovieData[i].conUrl = self.conUrl;
              likeMovieData[i].mhName = self.mhurlN;
            }
          }
          localStorage.setItem("enshrineData", JSON.stringify(likeMovieData));
        });
      }

      if (this.$route.query.index != undefined) {
        // 章节列表
        let mhurl = this.$route.query.mhurl;
        // console.log("mhurl1 ==>> ", mhurl);

        // 章节名
        self.mhurlN = mhurl[i].num;
        // console.log("mhurlN ==>> ", self.mhurlN);

        // 章节id
        self.conUrl = mhurl[this.i].url;
        // console.log("conUrl ==>> ", self.conUrl);
      }

      // let likeMovieData = localStorage.getItem("enshrineData");
      // likeMovieData = likeMovieData ? JSON.parse(likeMovieData) : [];
      // console.log("likeMovieData ==>> ", likeMovieData);

      // for (let i = 0; i < likeMovieData.length; i++) {
      //   if (likeMovieData[i].mhUrl == URL) {
      //     likeMovieData[i].i = this.i;
      //     likeMovieData[i].conUrl = self.conUrl;
      //     likeMovieData[i].mhName = self.mhurlN;
      //   }
      // }
      // localStorage.setItem("enshrineData", JSON.stringify(likeMovieData));

      this.axios({
        methods: "GET",
        url: "http://api.pingcc.cn/?mhurl2=" + self.conUrl
      }).then(result => {
        // console.log("result ==>> ", result);
        this.$store.commit("contentModule/getcontentData", result);
        this.$toast.clear();
        this.$refs.contentBox.scrollTop = 0;
        // console.log(" this.$refs.contentBox.scrollTop ",  this.$refs.contentBox.scrollTop)
      });
    },

    goto() {
      if (this.i > 0) {
        this.$toast.loading({
          duration: 0,
          message: "加载中..."
        });

        if (this.$route.query.i != undefined) {
          // this.i = this.$route.query.i;
          // console.log("this.i ==>> ", this.i);
          this.i--;
          // console.log("this.i ==>> ", this.i);
        }

        if (this.$route.query.index != undefined) {
          this.i--;
        }

        this.gotoSection();
      } else {
        this.i = 0;
        this.$toast.clear();
      }
    },

    getto() {
      if (this.i < this.mhurlL) {
        // console.log("this.mhurlL ==>>> ", this.mhurlL);
        this.$toast.loading({
          duration: 0,
          message: "加载中..."
        });

        if (this.$route.query.i != undefined) {
          // this.i = this.$route.query.i;
          // console.log("this.i ==>> ", this.i);
          this.i++;
          // console.log("this.i ==>> ", this.i);
        }

        if (this.$route.query.index != undefined) {
          this.i++;
        }

        this.gotoSection();
      } else {
        this.i = this.mhurlL.length - 1;
        this.$toast.clear();
      }
    }
  },
  created() {
    this.$toast.loading({
      duration: 0,
      message: "加载中..."
    });

    // 重置state
    this.resetState();

    let self = this;

    this.i = this.$route.query.index;

    // 名字id
    let URL = this.$route.query.mhUrl;
    // console.log("URL ==>> ", URL);

    this.mhUrl = URL;
    // console.log("this.mhUrl ==>> ", this.mhUrl);
    
    let URLss = this.$route.query;
    // console.log("URLss ==>> ", URLss);

    this.axios({
      methods: "GET",
      url: "http://api.pingcc.cn/?mhurl1=" + URL
    }).then(results => {
      // console.log("results ==>> ", results);
      // console.log("results.list ==>> ", results.data.list);
      self.mhurlL = results.data.list.length;
      // console.log("mhurlL ==>> ", self.mhurlL);
    });

    if (this.$route.query.mhurl == undefined) {
      self.conUrl = this.$route.query.url;
      // console.log("conUrl ==>> ", self.conUrl);

      self.mhurlN = this.$route.query.mhName;
      // console.log("mhurlN ==>> ", self.mhurlN);

      self.i = this.$route.query.i;
      // console.log(" self.i ==>> ", self.i);

      this.axios({
        methods: "GET",
        url: "http://api.pingcc.cn/?mhurl2=" + self.conUrl
      }).then(result => {
        // console.log("result ==>> ", result);
        this.$store.commit("contentModule/getcontentData", result);
        this.$toast.clear();
      });
      return;
    }

    // 章节列表
    let mhurl = this.$route.query.mhurl;
    // console.log("mhurl ==>> ", mhurl);

    // 章节id
    self.conUrl = mhurl[this.i].url;
    // console.log("conUrl ==>> ", self.conUrl);

    // 第...章节名
    self.mhurlN = mhurl[this.i].num;
    // console.log("mhurlN ==>> ", self.mhurlN);

    let likeMovieData = localStorage.getItem("enshrineData");
    likeMovieData = likeMovieData ? JSON.parse(likeMovieData) : [];
    // console.log("likeMovieData ==>> ", likeMovieData);

    for (let i = 0; i < likeMovieData.length; i++) {
      if (likeMovieData[i].mhUrl == URL) {
        likeMovieData[i].conUrl = self.conUrl;
        likeMovieData[i].mhName = mhurl[this.i].num;
        likeMovieData[i].i = self.i;
      }
    }

    localStorage.setItem("enshrineData", JSON.stringify(likeMovieData));

    self.mhurlL = mhurl.length;
    // console.log("mhurlL ==>> ", self.mhurlL);

    // console.log("this.i ", this.i);
    this.axios({
      methods: "GET",
      url: "http://api.pingcc.cn/?mhurl2=" + mhurl[self.i].url
    }).then(result => {
      // console.log("result ==>> ", result);
      this.$store.commit("contentModule/getcontentData", result);
      this.$toast.clear();
    });
  }
};
</script>
<style >
.content .van-nav-bar .van-icon {
  color: #e4393c;
}
.content .van-nav-bar__text {
  color: #e4393c;
  font-size: 16px;
}
.content .van-nav-bar__title {
  color: #e4393c;
  font-size: 20px;
}
</style>
<style lang="less" scoped>
.content {
  .content-box {
    width: 100%;
    position: fixed;
    top: 46px;
    bottom: 60px;
    right: 0;
    left: 0;
    overflow-y: auto;
    .btn {
      height: 56px;
      background: black;
      position: fixed;
      bottom: 0px;
      right: 0;
      left: 0;
      .van-button--normal {
        margin: 5px 12px;
        width: 100px;
      }
    }
  }
}
</style>